<script>
  import { mapState, mapMutations, mapAction, mapActions } from 'vuex'

  var baxeX = null
  export default {
    baxeX,
    created: function () {
      baxeX = this
    },
    data () {
      return {

      }
    },
    computed: {
      ...mapState(['$NearbyAttractionsList']),
      ...mapState(['$locationLat']),
      ...mapState(['$locationLng']),
      ...mapState(['$loading']),
      ...mapState(['$Details']),
      ...mapState(['$PhotosUrl']),
    },
    methods: {
      ...mapMutations(['$PostValue'])
    }
  }
</script>



