<template>
  <div>
    <div id="map"></div>
    <mt-search
      v-show="$route.path=='/nearby-attractions'"
      v-model="value"
      cancel-text="取消"
      placeholder="搜索"
      style="height:10%;"
    >
    </mt-search>
    <router-view></router-view>
    <mt-tabbar
      v-model="selected"
      style="position: fixed;bottom: 0px;height:10vh;"
    >
      <mt-tab-item id="nearby-attractions">
        <div
          @click="goto('/nearby-attractions')"
          class="mt-tab-item"
        >
          <div class="near"></div>
          附近景點
        </div>
      </mt-tab-item>
      <mt-tab-item id="popular-destination">
        <div
          class="mt-tab-item"
          @click="goto('/popular-destination')"
        >
          <div class="hot"></div>
          熱門景點
        </div>
      </mt-tab-item>
      <mt-tab-item id="tab3">
        <div class="mt-tab-item">
          <div class="new"></div>
          新進熱點
        </div>
      </mt-tab-item>
      <mt-tab-item id="tab4">
        <div class="mt-tab-item">
          <div class="news"></div>
          旅遊手記
        </div>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
  import globalDataX from '@/store/globalDataX.vue'
  export default {
    extends: globalDataX,
    data () {
      return {
        value: '',
        selected: 'nearby-attractions',
        pyrmont: new google.maps.LatLng(24.1441317, 120.69568709999999),
        service: null,
        map: null,
        markers: [{
          position: {
            lat: 24.0906165,
            lng: 120.6947551
          },
          infoText: '<strong>Marker 1</strong>'
        }],
      }
    },
    watch: {
      '$route' (val) {
        if (val.path.indexOf('/nearby-attraction-details/') > -1) {
          this.getPlaceDetails()
        }
      }
    },
    mounted () {
      console.log('-----------mounted初始化---------------')
      navigator.geolocation.getAccurateCurrentPosition(this.onSuccess, this.onError, this.onProgress, { desiredAccuracy: 20, maxWait: 15000 })
    },
    methods: {
      DetailsCallback (place, status) {
        if (status == google.maps.places.PlacesServiceStatus.OK) {
          this.$PostValue({ paramName: '$Details', data: place });
          this.$PostValue({ paramName: '$PhotosUrl', data: place.photos[0].getUrl({ maxWidth: document.body.clientWidth, maxHeight: document.body.clientHeight * 0.2 }) })
          // createMarker(place);
        }
      },
      goto (path) {
        this.$router.push(path)
      },
      onSuccess (data) {
        console.log(data)
        if (this.$DIS.GetDistance(this.$locationLat, this.$locationLng, data.coords.latitude, data.coords.longitude) < 1000) {
          this.$PostValue({ paramName: '$locationLat', data: data.coords.latitude })
          this.$PostValue({ paramName: '$locationLng', data: data.coords.longitude })
          return
        } else {
          this.$PostValue({ paramName: '$locationLat', data: data.coords.latitude })
          this.$PostValue({ paramName: '$locationLng', data: data.coords.longitude })
          // if(this.$NearbyAttractionsList.length!=0&&this.$locationLng)
          this.pyrmont = new google.maps.LatLng(data.coords.latitude, data.coords.longitude)
          this.map = new google.maps.Map(document.getElementById('map'), {
            center: this.pyrmont,
            zoom: 15
          });

          var request = {
            location: this.pyrmont,
            radius: '5000',
            type: ['tourist_attraction']
          };

          this.service = new google.maps.places.PlacesService(map);
          this.service.nearbySearch(request, this.getNearbyAttractionsCallback);
          // this.$AJ.getNearbyAttractions(data, this.getNearbyAttractionsCallback)
          this.markers = [{ position: { lat: data.coords.latitude, lng: data.coords.longitude } }]
          if (this.$route.path.indexOf('/nearby-attraction-details/') > -1) {
            this.getPlaceDetails()
          }
        }
      },
      getPlaceDetails () {
        var request = {
          placeId: this.$route.params.id,
          fields: ['name', 'rating', 'formatted_phone_number', 'geometry', 'reviews', 'photos', 'formatted_address']
        };
        this.service.getDetails(request, this.DetailsCallback);
      },
      onError (data) {
        console.log(data)
      },
      onProgress (data) {
        console.log(data)
      },
      getNearbyAttractionsCallback (results, status) {
        if (status == google.maps.places.PlacesServiceStatus.OK) {
          console.log(results)
          this.$PostValue({ paramName: '$NearbyAttractionsList', data: results })
          this.$PostValue({ paramName: '$loading', data: false })
        }
      },
    }
  }
</script>

<style>
</style>



