import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
var myState = {
  $NearbyAttractionsList: [],
  $locationLat: '', // 所在經緯度
  $locationLng: '',
  $loading: true,
  $Details: '',
  $PhotosUrl: ''
}

const store = new Vuex.Store({
  state: myState,
  mutations: {
    $PostValue (state, value) { // 根據參數更改vuex值
      state[value.paramName] = value.data
    }
  }
})
export default store



