import DistanceComputed from '@/PublicFuction/DistanceComputed.vue'

const GlobalFunction = {
    install (Vue, options) {
        let dis = new Vue({
            name: 'DIS',
            extends: DistanceComputed
        })
        Vue.prototype.$DIS = dis
    }
}

export default GlobalFunction



