<template>
  <div class="Nearby">
    <!-- <GmapMap
      :center="markers[0].position"
      :zoom="zoom"
      map-type-id="terrain"
      style="width: 500px; height: 300px"
    >
      <GmapMarker
        @click="marker()"
        ref="myMarker"
        :clickable="true"
        :draggable="true"
        :position="markers[0].position"
      />
    </GmapMap> -->

    <div
      v-for="(item,index) in $NearbyAttractionsList"
      :key="index"
      class="na-card"
      @click="clickNearByAttraction(item.place_id)"
    >
      <img
        class="na-img"
        :src="item.photos[0].getUrl({'maxWidth': 400, 'maxHeight': 400})"
        alt=""
      >
      <div class="na-decript">
        <img
          style="width:30px;height:30px;float:left;margin-top: -5px"
          :src="item.icon"
          alt=""
        >
        <span class="na-title">{{item.name}}</span><br>
        <hr style="margin-top: -1px">
        地址:{{item.vicinity}}<br>
        獲得總星數:{{item.user_ratings_total}}<br>
        星數:{{item.rating}}<div class="star"></div><br>
        距離:{{$DIS.GetDistance($locationLat, $locationLng, item.geometry.location.lat(), item.geometry.location.lng())>1000?$DIS.GetDistance($locationLat, $locationLng, item.geometry.location.lat(), item.geometry.location.lng())/1000+' 公里':$DIS.GetDistance($locationLat, $locationLng, item.geometry.location.lat(), item.geometry.location.lng())+' 公尺'}}
      </div>
    </div>
  </div>
</template>
<script>
  import globalDataX from '@/store/globalDataX.vue'
  export default {
    name: 'NearbyAttractions',
    extends: globalDataX,
    data () {
      return {
        loading: true,
      }
    },
    mounted () {
    },
    methods: {
      marker () {
        this.zoom = 20
      },
      clickNearByAttraction (id) {
        this.$router.push('/nearby-attraction-details/' + id)
      }
    }
  }
</script>

<style scoped>
.Nearby {
  width: 100%;
  height: 84vh;
  overflow-y: scroll;
  position: relative;
}
.na-card {
  height: 25%;
  display: flex;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  padding: 5px;
}
.na-card:active {
  background-color: rgba(242, 244, 246, 1);
}
.na-img {
  flex: 3;
  width: 30%;
}
.na-decript {
  flex: 7;
  padding: 10px;
}
.na-title {
  font-size: 1rem;
  color: #003061;
}

.demo-spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>



